export class Validator {
    constructor() { }
    static validateApiKey(apiKey) {
        if (!apiKey)
            return false;
        return apiKey.trim().length > 0;
    }
    static validateApiUrl(url) {
        try {
            const parsed = new URL(url);
            return parsed.protocol === 'http:' || parsed.protocol === 'https:';
        }
        catch {
            return false;
        }
    }
    static validateModelName(model) {
        return model.trim().length > 0;
    }
    static isEmptyValue(value) {
        if (value === undefined || value === null)
            return true;
        if (typeof value === 'string' && value.trim().length === 0)
            return true;
        if (Array.isArray(value) && value.length === 0)
            return true;
        if (typeof value === 'object' && Object.keys(value).length === 0)
            return true;
        return false;
    }
    static validateRequiredFields(fields, required) {
        const errors = [];
        for (const field of required) {
            if (Validator.isEmptyValue(fields[field])) {
                errors.push(`Missing or empty required field: ${field}`);
            }
        }
        return {
            isValid: errors.length === 0,
            errors,
        };
    }
}
export function isOpenAIResponse(data) {
    return (typeof data === 'object' &&
        data !== null &&
        'choices' in data &&
        Array.isArray(data.choices) &&
        data.choices.length > 0 &&
        'message' in data.choices[0] &&
        'usage' in data);
}
export function isGeminiResponse(data) {
    return typeof data === 'object' && data !== null && 'candidates' in data;
}
export function isClaudeResponse(data) {
    return (typeof data === 'object' &&
        data !== null &&
        'content' in data &&
        Array.isArray(data.content) &&
        'model' in data &&
        typeof data.model === 'string');
}
export function isMistralResponse(data) {
    return (typeof data === 'object' &&
        data !== null &&
        'choices' in data &&
        Array.isArray(data.choices) &&
        data.choices.length > 0 &&
        'message' in data.choices[0] &&
        'content' in data.choices[0].message &&
        'usage' in data);
}
export function isOllamaResponse(data) {
    return (typeof data === 'object' &&
        data !== null &&
        'response' in data &&
        typeof data.response === 'string' &&
        'done' in data &&
        typeof data.done === 'boolean');
}
export function isDeepseekResponse(data) {
    return (typeof data === 'object' &&
        data !== null &&
        'id' in data &&
        typeof data.id === 'string' &&
        'object' in data &&
        typeof data.object === 'string' &&
        'created' in data &&
        typeof data.created === 'number' &&
        'model' in data &&
        typeof data.model === 'string' &&
        'choices' in data &&
        Array.isArray(data.choices) &&
        data.choices.length > 0 &&
        'message' in data.choices[0] &&
        'content' in data.choices[0].message &&
        'usage' in data &&
        typeof data.usage === 'object' &&
        'total_tokens' in data.usage &&
        typeof data.usage.total_tokens === 'number');
}
export function isZaiResponse(data) {
    if (typeof data !== 'object' || data === null) {
        return false;
    }
    const obj = data;
    // Prfe choices Array
    if (!('choices' in obj) || !Array.isArray(obj.choices) || obj.choices.length === 0) {
        return false;
    }
    const firstChoice = obj.choices[0];
    // Prfe message Object
    if (!('message' in firstChoice) ||
        typeof firstChoice.message !== 'object' ||
        firstChoice.message === null) {
        return false;
    }
    const message = firstChoice.message;
    // Prfe content STRING!
    if (!('content' in message) || typeof message.content !== 'string') {
        return false;
    }
    // Prfe usage
    if (!('usage' in obj) || typeof obj.usage !== 'object') {
        return false;
    }
    return true;
}
